import { lazy } from "react";
const TestsCard = lazy(() => import("./TestsCard"));

const Tests = ({ activeTest }) => {
  return (
    <section
      className={`tests container ${activeTest ? "tests-click-style" : ""}`}
    >
      <h2 data-aos="fade-right" data-aos-duration="600" data-aos-once="true">
        Testlar
      </h2>
      <div className="row gx-5">
        <TestsCard />
      </div>
    </section>
  );
};

export default Tests;
